import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AutoUpdateComponent } from './auto-update/auto-update.component';
import { EmployeeComponent } from './employee/employee.component';
import { MenuComponent } from './menu/menu.component';
import { PermissionInstructionComponent } from './permission-instruction/permission-instruction.component';
import { RoleComponent } from './role/role.component';
import { UserLogComponent } from './user-log/user-log.component';
import { UserInforComponent } from './user-infor/user-infor.component';

const routes: Routes = [
  { path: '', redirectTo: 'index', pathMatch: 'full' },
  { path: 'index', component: EmployeeComponent },
  { path: 'employee', component: EmployeeComponent},
  {
    path:'menu',
    component:MenuComponent
  },
  {
    path:'auto-update',
    component:AutoUpdateComponent
  },
  {
    path:'role',
    component:RoleComponent
  },
  {
    path:'user-infor',
    component:UserInforComponent
  },
  {
    path:'user-log',
    component:UserLogComponent
  },
  {
    path:'permission-instruction',
    component:PermissionInstructionComponent
  }
];
export const SystemRouter = RouterModule.forChild(routes);

