import { Component, OnInit, AfterViewInit } from '@angular/core';
import * as lodash from 'lodash';
import * as GetElementDataHelper from '../../../../helper/GetElementDataHelper';
import { DataService } from '../../../../shared/services/data.service';
import { NotificationService } from '../../../../shared/services/notification.service';
import { UtilityService } from '../../../../shared/services/utility.service';
import { AuthenService } from '../../../../shared/services/authen.service';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { MessageContstants } from '../../../../shared/common/message.constants';
//src\shared\auth\token.interceptor.ts

@Component({
  selector: 'app-employee',
  templateUrl: './employee.component.html',
  styleUrls: ['./employee.component.css']
})



export class EmployeeComponent implements OnInit, AfterViewInit {

  const_list_employee: any[];
  tel_advance: string = null;
  email_advance: string = null;
  resource_sale_advance: string = null;
  work_place_advance: string = null;
  advance_search_result: any[] = [];
  is_searching = true;


  async ngAfterViewInit() {
  }
  public loadScript(url) {
    let body = <HTMLDivElement>document.body;
    let script = document.createElement('script');
    script.innerHTML = '';
    script.src = url;
    script.async = true;
    script.defer = true;
    body.appendChild(script);
  }
  constructor(private notificationService: NotificationService, private _dataService: DataService) { }


  /**
   * DEFINE VARIABLE 
   */
  list_employees: any[];
  public employee: any;
  default_img_url = "../../../../assets/assets_Desgin//assets/ftl-style/images/icons/img-default.png";
  url_ico = this.default_img_url;
  disabled = true;
  idToRemove = 0;
  idToEdit = 0;
  //employee: any = {};
  index = 0;


  ngOnInit() {
    this.getAll();
  }


  public getAll() {
    this._dataService.get('/api/v1/employee').subscribe((response: any[]) => {
      this.list_employees = response;
      this.const_list_employee = this.list_employees;
    }, error => this._dataService.handleError(error));
  }

  public delete(id: string) {
    this.notificationService.printConfirmationDialog(MessageContstants.CONFIRM_DELETE_MSG, () => {
      this._dataService.delete('/api/v1/employee/' + id).subscribe((response: any) => {
        this.notificationService.printSuccessMessage(MessageContstants.DELETED_OK_MSG);
        // this.search();
        this.getAll();
      }, error => this._dataService.handleError(error));
    });
  }
  //Save change for modal popup
  public saveChanges(valid: boolean) {
    if (valid) {
      // let fi = this.thumbnailImage.nativeElement;
      // if (fi.files.length > 0) {
      //   this.uploadService.postWithFile('/api/upload/saveImage?type=product', null, fi.files).then((imageUrl: string) => {
      //     this.entity.ThumbnailImage = imageUrl;
      //   }).then(() => {
      //     this.saveData();
      //   });
      // }
      // else {
      //   this.saveData();
      // }
      this.saveData();
      this.getAll();
    }
  }
  private saveData() {
    if (this.employee.id == undefined) {
      this.employee.workPlaceId = '069a4f1f-dc7e-492d-bc34-86ae431da808';
      this.employee.departmentId = '069a4f1f-dc7e-492d-bc34-86ae431da808';
      this.employee.position = 'OPS';
      this.employee.position = 'OPSSupervisor';
      this.employee.id = '024843-vh12';

      console.log(JSON.stringify(this.employee));
      this._dataService.post('/api/v1/employee', this.employee).subscribe((response: any) => {
        //this.search();
        this.notificationService.printSuccessMessage(MessageContstants.CREATED_OK_MSG);
      });
    }
    else {

      this._dataService.put('/api/v1/employee', JSON.stringify(this.employee)).subscribe((response: any) => {
        // this.search();
        this.notificationService.printSuccessMessage(MessageContstants.UPDATED_OK_MSG);
      }, error => this._dataService.handleError(error));
    }
  }

  public showAdd() {
    this.employee = { Content: '' };
  }
  public showEdit(id: string) {
    this._dataService.get('/api/v1/employee/GetById/' + id).subscribe((response: any) => {
      this.employee = response;
    }, error => this._dataService.handleError(error));
  }
  showHideDetail(i) {
    var el = $("tbody")[i].querySelector("tr.collapse-toggle-row");
    var tr = $(el);
    if (!tr.hasClass('shown')) {
      tr.addClass('shown');
      tr.siblings('.collapse-panel-row').removeClass('hidden');
    } else {
      tr.removeClass('shown');
      tr.siblings('.collapse-panel-row').addClass('hidden');
    }
  }

  advance_search() {
    this.list_employees = this.list_employees.filter(o => {
      return o.email == (this.email_advance == null ? o.email : this.email_advance) &&
        o.tel == (this.tel_advance == null ? o.tel : this.tel_advance) &&
        o.saleResource == (this.resource_sale_advance == null ? o.saleResource : this.resource_sale_advance) &&
        o.workPlaceId == (this.work_place_advance == null ? o.workPlaceId : this.work_place_advance);
    });

    var el = $("tr.collapse-toggle-row");
    var tr = $(el);
    if (!tr.hasClass('shown')) {
      tr.addClass('shown');
      tr.siblings('.collapse-panel-row').removeClass('hidden');
    }

  }

  abort_search() {
   
    this.tel_advance= null
    this.email_advance = null;
    this.resource_sale_advance = null;
    this.work_place_advance = null;
    this.list_employees = this.const_list_employee;

    var el = $("tr.collapse-toggle-row");
    var tr = $(el);

    tr.removeClass('shown');
    tr.siblings('.collapse-panel-row').addClass('hidden');


  }

}

